@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;500;600;900&display=swap');

* {
    font-family: 'Poppins', sans-serif;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: linear-gradient(300deg, #3A015C, #290025, #11001C);
    overflow-x: hidden;
}