main {
    color: white;
    height: 100vh;
    width: 100vw;
}

main .container {
    padding-top: 30vh;
    padding-left: 20vh;
    padding-right: 20vh;
    display: flex;
    justify-content: space-around;
}

.container .left-content {
    width: 50%;
}

.container .hero-text {
    margin-bottom: 40px;
}

.container .hero-text h1 {
    color: white;
    font-size: 47px;
    font-weight: 900;
    line-height: 1.2;
    margin: 20px 0px 20px;
}

span{
    color: #3A015C;
}

.container .hero-text p {
    width: 440px;
    max-width: 100%;
    color: white;
    font-size: 15px;
    line-height: 15px;
    margin-bottom: 30px;
}

.container .social {
    display: flex;
    gap: .5rem;
    align-items: center;
    margin-bottom: 30px;
}

.container .left-content button {
    font-size: 16px;
    font-weight: 600;
    padding: 8px 30px;
    border: 2px solid white;
    border-radius: 50px;
    background: #3A015C;
    color: white;
    line-height: 24px;
    transition: all .5s ease;
    cursor: pointer;
}

.container .left-content button:hover {
    background: transparent;
    border: 2px solid #3A015C;
    color: #3A015C;
}

.container .right-content {
    width: 40%;
}

.container .right-content .carousel-control-prev {
    display: none;
}

.container .carousel-control-prev-icon {
    display: none;
}

.container .right-content .carousel-control-next {
    display: none;
}

.container .carousel-control-next-icon {
    display: none;
}


.container .carousel-indicators [data-bs-target] {
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #3A015C;
} 


@media screen and (max-width: 768px) {
    main .container {
        width: 100%;
        height: auto;
        padding-left: 7vh;
        padding-right: 7vh;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    .container .left-content {
        width: 100%;
        height: 100vh;
    }

    .container .right-content {
        width: 100%;
        height: 70vh;
    }

    .container .hero-text h1 {
        font-size: 60px;
        
    }

    .container .hero-text p {
        width: 440px;
        max-width: 100%;
    }
}

@media screen and (max-width: 440px) {
    main .container {
        width: 100%;
        height: auto;
        padding-left: 20px;
        padding-right: 20px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    .container .left-content {
        width: 100%;
        height: 100vh;
    }

    .container .right-content {
        width: 100%;
        height: 70vh;
    }

    .container .hero-text h1 {
        font-size: 40px;
        
    }

    .container .hero-text p {
        width: 100%;
    }
}
