header {
    position: fixed;
    background: #11001C;
    height: 80px;
    width: 100%;
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    z-index: 100;
    box-shadow: 0px 1px 9px 0px rgba(0,0,0,0.70);
    -webkit-box-shadow: 0px 1px 9px 0px rgba(0,0,0,0.70);
    -moz-box-shadow: 0px 1px 9px 0px rgba(0,0,0,0.70);
}

.nav-brand h3 {
    font-weight: 900;
    margin-top: auto;
    margin-bottom: auto;
}

.nav-menu {
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    gap: 1.3rem;
    list-style: none;
}

.nav-link {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.919);
}

.nav-link:hover {
    color: white;
}

.nav-toggler div {
    width: 20px;
    height: 2px;
    margin: 4px;
    background: darkgray;
    transition: .4s ease-in;
}

.nav-toggler {
    cursor: pointer;
    display: none;
}

@media screen and (max-width: 768px) {
    .nav-toggler {
        display: block;
    }

    .nav-menu {
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        top: 80px;
        right: 0;
        width: 50%;
        height: calc(100vh - 80px);
        background: #11001C;
        transform: translateX(100%);
        transition: 0.4s ease-in;
    }
}

.nav-active {
    transform: translateX(0);
}

.toggle .line1{
    transform: rotate(-135deg) translate(-4px, -5px);
    transition: .4s ease-out;
}

.toggle .line2{
    opacity: 0;
    transform: scale(0.1);
    transition: .4s ease-out;
}

.toggle .line3{
    transform: rotate(135deg) translate(-3px, 5px);
    transition: .4s ease-out;
}